.nav {
    position: fixed;
    top: 0;
    display: flex; 
    padding: 10px 20px;
    width: 100%;
    transition: all 200ms;
    z-index: 1;
}

.nav_logo {    
    width: 70px;  
    object-fit: contain;  
}

.nav_black {
    transition: all 200ms;
    opacity: 0.95;
    background-color: #111;
}

.nav_name {
    font-weight: bolder;
    font-size: 30px;
    color: yellow;
}