.banner {
    padding-top: 130px;
    color: white;
    object-fit: contain;
    height: 56.25%;
    margin-bottom: 20px;
}

.banner_contents {
    margin-left: 30px;
    padding-top: 100px;
    height: 190px;
}

.banner_title {
    font-size: 2.5rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    margin-top: 1rem;    
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;    
    background-color: rgba(51, 51, 51, 0.5);
}

.banner_button:hover {
    transition: all 300ms;
    color: #111;
    background-color: white;
}

.banner_fade {
    height: 4.4rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.61),
        #111
    );
}